body {
  --size-stopwatch: 80vmin;
  --size-center: 5vmin;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
}

body .stopwatch {
  height: var(--size-stopwatch);
  width: var(--size-stopwatch);
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

body .stopwatch .stopwatch__center {
  height: var(--size-center);
  width: var(--size-center);
  background-color: #f6a603;
  border-radius: 50%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

body .stopwatch .stopwatch__hand {
  transform-origin: 50% 100%;
  position: absolute;
  top: 50%;
  left: 50%;
}

body .stopwatch .stopwatch__hand--minutes {
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 3600s linear infinite rotate-minutes;
}

body .stopwatch .stopwatch__hand--seconds {
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 60s linear infinite rotate-seconds;
}

.stopwatch--speed-up.stopwatch__hand--minutes {
  animation-duration: 600s;
  animation-timing-function: steps(60, end);
}

.stopwatch--speed-up.stopwatch__hand--seconds {
  animation-duration: 10s;
}

@keyframes rotate-minutes {
  0% {
    transform: translate(-50%, -100%)rotate(0);
  }

  100% {
    transform: translate(-50%, -100%)rotate(360deg);
  }
}

@keyframes rotate-seconds {
  0% {
    transform: translate(-50%, -100%)rotate(0);
  }

  100% {
    transform: translate(-50%, -100%)rotate(360deg);
  }
}
